

@import "../variables.less";
@import "variables.less";
@import "mixins.less";
@import "../mixins.less";
@import "margin-padding.less";

//
// Buttons
// --------------------------------------------------

// Common styles
.btn-default,
.btn-primary,
.btn-success,
.btn-info,
.btn-warning,
.btn-danger {
  .box-shadow(none);

  // Reset the shadow
  &:active,
  &.active {
    .box-shadow(inset 0 3px 5px rgba(0,0,0,.125));
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    .box-shadow(none);
  }

  .badge {
    text-shadow: none;
  }
}

// Mixin for generating new styles
.btn-styles(@btn-color: #555) {
  .reset-filter(); // Disable gradients for IE9 because filter bleeds through rounded corners; see https://github.com/twbs/bootstrap/issues/10620
  border-color: darken(@btn-color, 14%);

  &:hover,
  &:focus  {
    background-color: darken(@btn-color, 12%);
    background-position: 0 -15px;
  }

  &:active,
  &.active {
    background-color: darken(@btn-color, 12%);
    border-color: darken(@btn-color, 14%);
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &.focus,
    &:active,
    &.active {
      background-color: darken(@btn-color, 12%);
      background-image: none;
    }
  }
}

// Common styles
.btn {
  // Remove the gradient for the pressed/active state
  &:active,
  &.active {
    background-image: none;
  }
}

// Apply the mixin to the buttons
.btn-default { .btn-styles(@btn-default-bg); border-color: #ccc; }
.btn-primary { .btn-styles(@btn-primary-bg); }
.btn-success { .btn-styles(@btn-success-bg); }
.btn-info    { .btn-styles(@btn-info-bg); }
.btn-warning { .btn-styles(@btn-warning-bg); }
.btn-danger  { .btn-styles(@btn-danger-bg); }


//
// Images
// --------------------------------------------------

.thumbnail,
.img-thumbnail {
  .box-shadow(0 1px 2px rgba(0,0,0,.075));
}


//
// Dropdowns
// --------------------------------------------------

.dropdown-menu > li > a:hover,
.dropdown-menu > li > a:focus {
  #gradient > .vertical(@start-color: @dropdown-link-hover-bg; @end-color: darken(@dropdown-link-hover-bg, 5%));
  background-color: darken(@dropdown-link-hover-bg, 5%);
}
.dropdown-menu > .active > a,
.dropdown-menu > .active > a:hover,
.dropdown-menu > .active > a:focus {
  #gradient > .vertical(@start-color: @dropdown-link-active-bg; @end-color: darken(@dropdown-link-active-bg, 5%));
  background-color: darken(@dropdown-link-active-bg, 5%);
}


//
// Navbar
// --------------------------------------------------

// Default navbar
.navbar-default {

  background-color: @navbar-default-bg;
  .reset-filter(); // Remove gradient in IE<10 to fix bug where dropdowns don't get triggered
  border-radius: @navbar-border-radius;
  @shadow: inset 0 1px 0 rgba(255,255,255,.15), 0 1px 5px rgba(0,0,0,.075);
  .box-shadow(@shadow);

  .open .dropdown-menu {
    background: #f8f8f8;
    border-top: 0;
  }

  .navbar-toggle {

    background-color: @navbar-default-link-hover-bg;
    border-color: darken(@navbar-default-link-hover-bg, 10%);

    &:hover,
    &:focus {
      background: darken(@navbar-default-link-active-color, 10%);
    }

    .icon-bar {
      background-color: @navbar-default-link-color;
    }
  }

  .navbar-brand {

    span {
      padding: 4px 10px;
      background: #285A70;
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
      border-left: 5px solid #042F3F;

    }

    &,
    &:hover,
    &:focus {
      color: @navbar-default-link-color;
      font-size: 20px;
    }
  }

  .navbar-nav > .open > a {
    &,
    &:hover,
    &:focus {
      color: @navbar-default-link-color;
      background: @navbar-default-link-active-bg;
    }
  }

  .navbar-nav {

    li > a {
      color: @navbar-default-link-color;
      font-size: @navbar-item-font-size;

      &:hover,
      &:focus {
        color: @navbar-default-link-color;
        background: @navbar-default-link-active-bg;
      }
    }

    .dropdown-menu li > a {

      color: #000;
      line-height: @navbar-nav-subtext-lineheight;
      font-size: @navbar-subitem-font-size;

      &:hover,
      &:focus {
        text-decoration: underline;
        background: none;
      }
    }
  }
}


// Undo rounded corners in static and fixed navbars
.navbar-static-top,
.navbar-fixed-top,
.navbar-fixed-bottom {
  border-radius: 0;
}

// Fix active state of dropdown items in collapsed mode
@media (max-width: @grid-float-breakpoint-max) {
  .navbar .navbar-nav .open .dropdown-menu > .active > a {
    &,
    &:hover,
    &:focus {
      color: #fff;
    }
  }
}


//
// Alerts
// --------------------------------------------------

// Common styles
.alert {
  text-shadow: 0 1px 0 rgba(255,255,255,.2);
  @shadow: inset 0 1px 0 rgba(255,255,255,.25), 0 1px 2px rgba(0,0,0,.05);
  .box-shadow(@shadow);
}

// Mixin for generating new styles
.alert-styles(@color) {
  #gradient > .vertical(@start-color: @color; @end-color: darken(@color, 7.5%));
  border-color: darken(@color, 15%);
}

// Apply the mixin to the alerts
.alert-success    { .alert-styles(@alert-success-bg); }
.alert-info       { .alert-styles(@alert-info-bg); }
.alert-warning    { .alert-styles(@alert-warning-bg); }
.alert-danger     { .alert-styles(@alert-danger-bg); }


//
// Progress bars
// --------------------------------------------------

// Give the progress background some depth
.progress {
  #gradient > .vertical(@start-color: darken(@progress-bg, 4%); @end-color: @progress-bg)
}

// Mixin for generating new styles
.progress-bar-styles(@color) {
  #gradient > .vertical(@start-color: @color; @end-color: darken(@color, 10%));
}

// Apply the mixin to the progress bars
.progress-bar            { .progress-bar-styles(@progress-bar-bg); }
.progress-bar-success    { .progress-bar-styles(@progress-bar-success-bg); }
.progress-bar-info       { .progress-bar-styles(@progress-bar-info-bg); }
.progress-bar-warning    { .progress-bar-styles(@progress-bar-warning-bg); }
.progress-bar-danger     { .progress-bar-styles(@progress-bar-danger-bg); }

// Reset the striped class because our mixins don't do multiple gradients and
// the above custom styles override the new `.progress-bar-striped` in v3.2.0.
.progress-bar-striped {
  #gradient > .striped();
}


//
// List groups
// --------------------------------------------------

.list-group {
  border-radius: @border-radius-base;
  .box-shadow(0 1px 2px rgba(0,0,0,.075));
}
.list-group-item.active,
.list-group-item.active:hover,
.list-group-item.active:focus {
  text-shadow: 0 -1px 0 darken(@list-group-active-bg, 10%);
  #gradient > .vertical(@start-color: @list-group-active-bg; @end-color: darken(@list-group-active-bg, 7.5%));
  border-color: darken(@list-group-active-border, 7.5%);

  .badge {
    text-shadow: none;
  }
}


//
// Panels
// --------------------------------------------------

.panel-default {

  .panel-heading {
    color: #fff;
    background: @theme-bg-active-color;
    border-bottom: 1px solid @theme-bg-more-active-color;

    &.active {
      background-color: #285A70;
    }
  }
}


//
// Wells
// --------------------------------------------------

.well {
  #gradient > .vertical(@start-color: darken(@well-bg, 5%); @end-color: @well-bg);
  border-color: darken(@well-bg, 10%);
  @shadow: inset 0 1px 3px rgba(0,0,0,.05), 0 1px 0 rgba(255,255,255,.1);
  .box-shadow(@shadow);
}

.panel-default {

  .panel-body {

    hr {
      border-top-color: #ccc;;
    }
  }
}

// -------- Megamenu ----------

.md-megamenu {

  min-height: @navbar-height;

  .navbar-nav > li > .dropdown-menu {
    margin-top: 2px;
  }

  .navbar-right {
    margin-right: 0;
  }

  // reset positions
  .nav, .collapse, .dropup, .dropdown {
    position: static;
  }

  // propagate menu position under container for fw navbars
  .container {
    position: relative;
  }

  // by default aligns menu to left
  .dropdown-menu {
    left: auto;

    -webkit-transform-origin: top;
    transform-origin: top;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
    -webkit-transform: scale(1, 0);
    display: block;

    transition: all 0.2s ease-out;
    -webkit-transition: all 0.2s ease-out;
  }

  .open > .dropdown-menu {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
  }

  // Fullwidth menu
  .dropdown.yamm-fw .dropdown-menu {
    left: 0; right: 0;
  }

  .md-megamenu-content {
    padding: 5px 15px 0;

    ul {

      margin-top: 5px;

      li:before {
        position: relative;
        top: 1px;
        display: inline-block;
        font-family: @font-family-icon;
        -webkit-font-smoothing: antialiased;
        padding: 0 5px;
        content:"\f005";
        color: #ddd;
      }
    }

    .md-megamenu-subtitle {
      background: #ddd;
      padding: 3px 10px;
      border-radius: 3px;
      font-weight: bold;
      line-height: @navbar-nav-subtext-lineheight;
    }
  }

}

// ====== Modal Dialog =======

.modal-dialog {
  .modal-header {
    background-color: @theme-bg-color;

    .modal-title {
      font-size: 15px;
      color: #fff;
    }
  }
}

// ========== Header =============

#header {
  margin: 0 -15px;
}

// ========== Left Side =============

#leftSidebar {
  position: fixed;
  height: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  width: inherit;

  .navbar-header {
    width: inherit;

    .leftCollapser {
      float: right;
      position: relative;
      right: 10px;
      top: 6px;
      background: #13475d;
      font-size: 18px;
      border-radius: 11px;
      border: 1px solid #032B3D;
    }
  }

  .navbar-default {
    //background-color: #2F6E89;
    border: none;
    border-radius: 0;
    height: 100%;
    .box-shadow(none);
  }

  .leftMenu {

    @navbar-menu-selected-animation: all 0.2s ease-out;

    float: left;
    width: 100%;

    .navbar-collapse {
      padding-left: 0;
      padding-right: 0;
    }

    .panel-group {

      .panel-heading + .panel-collapse > .panel-body {
        border-top: 0;
      }

      .panel {
        -webkit-border-radius: 0;
        -moz-border-radius: 0;
        border-radius: 0;
      }

      .panel + .panel {
        margin-top: 0;
      }
    }

    .navbar-header {
      width: inherit;
      .box-shadow(0 4px 4px -2px rgba(0, 0, 0, 0.4))
    }

    .leftMenuLinks {
      float: left;
      width: inherit;
      padding-top: 10px;
    }

    .panel-default {

      background: none;
      border: none;

      .panel-heading {
        border-radius: 0;
        .transition(@navbar-menu-selected-animation);

        .panel-title {
          font-size: @navbar-item-font-size;
        }
      }
    }

    .panel-collapsed > .panel-body {
      display: none;
    }

    .panel-body {
      padding: 10px 1px 10px 5px;

      .nav-pills > li > a {

        padding: 8px;
        font-size: @navbar-subitem-font-size;
        .transition(@navbar-menu-selected-animation);

        &.active,
        &:hover,
        &:active {
          background: #042F3F;
          border-right: 5px solid #eee;
          -webkit-border-radius: 0;
          -moz-border-radius: 0;
          border-radius: 0;
        }
      }
    }

    a {
      color: #fff;
    }
  }
}

#bodyContent {
  padding: 0 20px;
}

#footer {
  margin-top: 40px;
}

.footer {
  text-align: center;
  padding: 10px;
  background: #dedede;
}

.text-underline {
  text-decoration: underline;
}

.nav-v-menu {

  .nav-stacked{
    float: left;
    width: 20%;
    border-right: 1px solid #ddd;
    padding-right: 10px;
  }

  .tab-content {
    float: left;
    width: 80%;
  }
}

.nav-tabs,
.nav-pills {

  li {

    &.active {

      a {

        &,
        &:hover {
          background: @navbar-default-bg;
          color: @navbar-default-color;
          border: 1px solid @navbar-default-border;
        }
      }
    }
  }
}

.panel-form {

  @color: #f5f5f5;
  border-color: darken(@color, 10%);

  .panel-heading {
    background: @theme-bg-hover-color;

    .panel-title {
      color: @theme-text-color;
      font-weight: bold;
    }
  }

  .panel-body {
    background: @color;
  }

  .panel-footer {
    background: darken(@color, 2%);
  }
}

// ui-grid

.ui-grid-cell div {
  padding: 3px;
}

.cell-center {
  text-align:center;
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.ui-grid-center div {
  .cell-center();
}

.ui-grid-boldtext {
  font-weight: bold;
  font-size: 16px;
}

div.ui-grid-cell input[type="checkbox"] {
  margin: auto;
  width: 100%;
}

.ui-grid-icon-ok:before {
  content: '\f096';
}

.ui-grid-icon-angle-down:before {
  content: '\f0c9';
}

.ui-grid-icon-up-dir:before {
  content: '\f0de';
}

.ui-grid-icon-down-dir:before {
  content: '\f0dd';
}

.ui-grid-row-selected .ui-grid-icon-ok:before,
.ui-grid-all-selected.ui-grid-icon-ok:before {
  content: '\f046' !important;
}

[class^="ui-grid-icon"]:before, [class*=" ui-grid-icon"]:before {
  font-family: @font-family-icon;
  margin: 0;
  width: auto;
}

.ui-grid-selection-row-header-buttons {
  opacity: 1;
}

// ================= Auth ==================

body.singleCol {
  background: @theme-bg-color;
}

#authContainer {
  margin-top: 150px;
  max-width: 450px;
}

#authContainer .panel {
  -webkit-box-shadow: 1px 3px 5px 1px rgba(48,50,51,0.7);
  -moz-box-shadow: 1px 3px 5px 1px rgba(48,50,51,0.7);
  box-shadow: 1px 3px 5px 1px rgba(48,50,51,0.7);
}

// ============ Form Validation ==============
.ng-dirty .ng-invalid {
  .form-control-validation(@state-danger-text; @state-danger-text; @state-danger-bg);
}

.validation-invalid {
  color: @state-danger-text;
}

.form-group .help-block {
  margin-bottom: 0;

}

.md-form-spinner-container {
  position: relative;

  .md-form-spinner {
    position: relative;
    top: 45%;
    left: 45%;
    color: @theme-bg-color;
  }

  .md-form-overlay {
    opacity: 0.6;
    background: #ccc;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 9999;
  }
}

.row-disable {
  background-color: #ccc !important;
  opacity: 0.4;
}
